.custom-select {
  position: relative;
  width: 100%;
  max-width: 300px;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__button {
  border-radius: 5px 5px 0 0;
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select__label {
  display: block;
  margin: 0 0 10px;
  font-weight: 0;
  font-size: 16px;
  line-height: 24px;
}
.custom-select__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 5px;
  border-radius: 5px;
  background-color: lightgray;
  cursor: pointer;
  outline: none;
}
.custom-select__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: 10px;
  background-color: #000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select__list {
  z-index: 1;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  max-height: 102px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: lightgrey;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
}
.custom-select__item {
  padding: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  transition: background-color 0.3s ease;
  cursor: pointer;
  outline: none;
}
@media (hover: hover) {
  .custom-select__item:hover:not(.focus-visible) {
    background-color: chartreuse;
  }
}
.custom-select__item.focus-visible:focus {
  background-color: chartreuse;
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

