.intro {
  position: relative;
  overflow: hidden;

  &__background {
    position: absolute;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    padding: 150px 0;

    @include vp-1023 {
      padding: 90px 0;
    }
  }

  &__title {
    margin: 0;

    font-family: "Hiragino Kaku Gothic Std", "Arial", sans-serif;
    font-weight: 800;
    font-size: 64px;
    line-height: 64px;

    color: $color-default-black;

    @include vp-1023 {
      font-size: 54px;
      line-height: 59px;
    }

    @include vp-767 {
      font-size: 40px;
      line-height: 44px;
    }
  }

  &__description {
    max-width: 582px;
    margin: 28px 0 0;

    font-weight: 400;
    font-size: 20px;
    line-height: 30px;

    color: $color-default-black;

    @include vp-1023 {
      max-width: 320px;
      margin: 20px 0 0;

      font-size: 18px;
      line-height: 27px;
    }
  }

  &__button {
    width: 100%;
    max-width: 300px;
    margin: 40px 0 0;

    @include vp-1023 {
      max-width: 320px;
    }

    @include vp-767 {
      order: 1;
      margin: 20px 0 0;
    }
  }

  &__image {
    z-index: -1;
    position: absolute;

    top: 0;
    right: 0;

    width: 1060px;
    height: 582px;

    @include vp-1439 {
      right: unset;
      left: 379px;
    }

    @include vp-1023 {
      top: 176px;
      left: 283px;

      width: 485px;
      height: 265px;
    }

    @include vp-767 {
      position: relative;
      top: 0;
      left: 0;

      width: calc(100% + 35px * 2);
      height: 328px;
      margin: 0 -35px;
    }

    @include vp-374 {
      width: calc(100% + 20px * 2);
      margin: 0 - 20px;
    }

    img {
      width: 1060px;
      height: 582px;

      object-fit: cover;

      @include vp-1023 {
        width: 485px;
        height: 265px;
      }

      @include vp-767 {
        width: 600px;
        height: 328px;
      }
    }
  }
}
